import _ from 'underscore'

const AppStore = (store, action) => {
    if(_.isEmpty(store)) {
        store = createEmptyStore()
    }
    switch(action.type) {
        case 'LOAD_MODELS' :
            return {
                'modList': loadModList(),
                'config': loadConfig(),
                'profile': loadProfile(),
                'state': getMainState()
            }
        default:
            return store
    }
}

const loadModList = () => {
    return {
        'mod1': {
            name: 'Mod1',
            active: false,
            filename: 'mod1.zip',
            url: null,
            version: null
        },
        'mod2': {
            name: 'Mod1',
            active: false,
            filename: 'mod1.zip',
            url: null,
            version: null
        },
        'mod3': {
            name: 'Mod1',
            active: false,
            filename: 'mod1.zip',
            url: null,
            version: null
        },
        'mod4': {
            name: 'Mod1',
            active: false,
            filename: 'mod1.zip',
            url: null,
            version: null
        }
    }
}

const loadConfig = () => {
    return {}
}

const loadProfile = () => {
    return {}
}

const createEmptyStore = () => {
    return {
        'modList': {},
        'config': {},
        'profile': {},
        'state': {}
    }
}

const getMainState = () => {
    return {
        page: 'main'
    }
}

export default AppStore